.ColorPaltte-container-2{
    width: 200px;
    height: 45px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 20px;
}

.ColorPaltte-container-2 div{
    width: 13px;
    height: 13px;
    background-color: beige;
    margin-right: 15px;
    margin-left: 15px;
    margin-bottom: 5px;
    
}
.ColorPaltte-container-2 div:hover{
    border: 1px solid rgb(253, 252, 252);
    margin-right: 14.5px;
    margin-left: 14.5px;
    margin-bottom: 2px;
}
