.add-text-container{
  position:absolute;
  right: 10px;
  bottom: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  
}
.add-text-feild{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: black;
    color: white;
    padding: 10px;
}
.add-text-input{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin-bottom: 10px;
}
.add-text-input input{
    border:none;
    outline: none;
    margin-top: 5px;
    height: 20px;
}
.add-text-fonts{
    width: 200px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    font-size: 13px;
}
.add-text-fonts div{
    margin: 5px 10px;
    cursor: pointer;
}
.add-text-pos-rot{
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 10px;
}
.add-text-pos{
    width: 80px;
    height: 60px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-bottom: 10px;

}
.add-text-pos p{
   font-size: 12px;
margin-top: 5px;
margin-bottom: -5px;
}

.add-text-pos-rot svg{
    width: 25px;
    height: 25px;
   margin-left: 40px;
    cursor: pointer;
    transition: 0.3s;
    margin-top: 15px;
}
.add-text-pos-rot svg:hover{
    width: 25px;
    height: 25px;
    transform: rotate(90deg);
 
}
.add-text-button{
    background-color: black;
    color: white;
    width: 90px;
    height: 20px;
    padding: 5px 10px;
    border-radius: 2px;
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}