.slider2{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .slider-bar-2{
    -webkit-appearance: none;
    margin-top: 5px;
    height: 2px;
    border-radius: 5px;
    background: #d3d3d3;
    outline: none;
    opacity: 0.7;
    -webkit-transition: .2s;
    transition: opacity .2s;
  
  }
  .slider-bar-2::-webkit-slider-thumb {
  
    -webkit-appearance: none;
    appearance: none;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background: #8d8d8d;
    cursor: pointer;
  }