.ColorPaltte-container{
    width: 90%;
    height: 45px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: 30px;
}
.ColorPaltte-container div{
    width: 13px;
    height: 13px;
    background-color: beige;
    margin-right: 30px;
    margin-bottom: 5px;
    transform: translateX();
}
.ColorPaltte-container div:hover{
    width: 15px;
    height: 15px;
    
    margin-right:28px;
    margin-bottom: 3px;
}
#red{
    background-color: red;
}
#green{
    background-color: green;
}
#blue{
    background-color: blue;
}
#yellow{
    background-color: yellow;
}
#black{
    background-color: rgb(41, 41, 41);
}
#pink{
    background-color: pink;
}
#white{
    background-color: white;
}
#violet{
    background-color: violet;
    
}
