
.optionsitem-container{
    
    display:flex ;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    outline: none;
    border: none;
    margin-bottom: 10px;
    
}


.options-button{
    font-size: 17px;
    font-weight: 500;
    transition:  0.2s;
    background-color: rgb(0, 0, 0);
    color: white;
    display:flex ;
    width: 100%;
    justify-content: space-around;
    align-items: center;
    height: 45px;
    cursor: pointer;
    outline: none;
    border: none;
    transition: 0.5s;
    margin-bottom: 10px;
    margin-top: 10px;
}
.options-button.active{
    font-size: 17px;
    font-weight: 500;
    transition:  0.2s;
    background-color: rgb(221, 221, 221);
    color: rgb(0, 0, 0);
    display:flex ;
    border-radius: 10px;
    width: 90%;
    justify-content: space-around;
    align-items: center;
    height: 45px;
    cursor: pointer;
    outline: none;
    border: none;
    transition: 0.5s;
    margin-bottom: 10px;
    margin-top: 10px;
}
.options-button-head{
    
    background-color: rgb(0, 0, 0);
    color: white;
    display:flex ;
    width: 100%;
    font-size: 20px;
    justify-content:center;
    align-items: center;
    height: 55px;
    cursor: pointer;
    outline: none;
    border: none;
}
.options-button-head svg{
    fill:white;
    width: 20px;
    height: 20px;
    margin-left:10px;
    margin-bottom: 2px;
}

.options-button:hover{
    font-size: 19px;
    
}
.options-button-head:hover{
    font-size: 22px;
    background-image: linear-gradient(to right,rgba(0, 0, 0, 0.369),rgb(29, 29, 29) ,rgba(0, 0, 0, 0.356));
    
}