::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(34, 34, 34);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}


.container {
  width: 100%;
  display: flex;
  justify-content: space-between !important;
  flex-grow: 2;
  height: 100vh;
  overflow: hidden;
}

.options-container {
  width: 210px;
  display: flex;
  padding-top: 20px;
  flex-direction: column;
  justify-content: flex-start;
  background-color: black;
  height: 100vh;
  overflow: scroll;
}
.upload-image{
  width: 500px;
  padding: 20px;
  border-radius: 20px;
  background: gray ;
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;

}
.upload-image h5{
margin-top: -10px;
font-family:cursive;


}

.img-container {
  position: relative;
  width: auto;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1;

}

.img-container svg {
  position: absolute;
  width: 30px;
  height: 30px;
  top: 8px;
  right: 20px;

}

.img-container svg:hover {
  top: 5px;
  right: 17px;
  width: 34px;
  height: 34px;


}

.edit-slider-container {
  position: absolute;
  left: 10px;
  top: 10px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

}


.main-image-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  display: block;
  z-index: -1;
  overflow: visible;
  

}

.main-image-container-2 {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  display: block;
  z-index: 3;
  min-width: 350px;
  min-height: 350px;



}

.img-text {
  position: absolute;
  top: 0;
  z-index: 2;
}

.main-image {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 0.3s;
  background-position: center center;
  background-repeat: no-repeat;
  

}
.app-flip-button{
  width: 100px;
  height: 30px;
  outline: none;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  position:absolute;
  right: 270px;
  bottom: 10px;
  color: white;
  background-color: black;
  border-radius: 2px;
  font-size: 18px;
  
}
.resize-button {
  position: absolute;
  top: 10px;
  right: 60px;
  outline: none;
  border: none;
  font-size: 15px;
  background-color: black;
  color: white;
  padding: 5px 20px;
  transition: 0.2s;
  cursor: pointer;

}

.resize-button:hover {
  top: 9px;
  right: 59px;
  font-size: 16px;
  background-color: rgb(195, 195, 195);
  color: rgb(0, 0, 0);
  padding: 5px 20px;

}

.resize-drop-down {
  position: absolute;
  top: 50px;
  right: 13px;
  display: flex;
  width: 190px;
  height: 90px;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background-color: black;
  color: white;
  font-size: 13px;
  padding: 9px;
}

.resize-drop-down div {
  transition: 0.5s;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 100%;
  cursor: pointer;
  margin-bottom: 3px;

}

.resize-drop-down svg {
  fill: rgb(0, 0, 0);
  position: absolute;
  top: -12px;
  right: 85px;
  width: 17px;
  height: 17px;
  transform: rotate(180deg);

}

.resize-drop-down div:hover {
  background-color: rgb(255, 253, 253);
  color: rgb(0, 0, 0);
  font-size: 14px;
}

.resize-button :hover {
  background-color: rgb(255, 253, 253);
  color: rgb(0, 0, 0);
  font-size: 14px;
}

.rotateicon {
  width: 30px;
  height: 30px;
  fill: white;
  margin-top: 5px;
  transition: 0.3s;
}

.rotateicon:hover {

  fill: white;
  transform: rotate(40deg);
  margin-top: 5px;
}

.app-sticker {
  position:absolute;
  top:0;
  left: 0;
  width: 20px;
  height: 20px;
}