::-webkit-scrollbar {
    width: 8px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: rgb(34, 34, 34); 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555; 
  }


.add-stickers-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 210px;
    position: absolute;
    right: 90px;
    bottom: 10px;
}
.add-stickers-box{
    display: flex;
    background-color: black;
    color: white;
    flex-direction: column;
    justify-content: space-around;
    padding:10px 15px;
    align-items: center;
    height: 250px;
    width: 180px;
}
.add-stickers-size{
    margin-top: 10px;
}
.add-stickers-posiion{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin-top: 0px;
}
.add-stickers-posiion div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin-top: 10px;
}
.add-stickers-posiion div p{

    margin-bottom: -10px;
}
.add-stickers-sticker-box{
    width: 100%;
    height: 110px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    overflow-y:scroll;
    background-color: white;
}
.add-stickers-sticker-box img{
    width: 40px;
    height: 40px;
    margin: 3px;
    cursor: pointer;
   
}
.rotateaddsticker {
    width: 15px;
    height: 15px;
    transition: 0.3s;
    margin-top: 5px;
    cursor: pointer;
    
}
.rotateaddsticker:hover{

    transform: rotate(120deg);
}
.add-stickers-button{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    color: white;
    width: 130px;
    height: 30px;
    cursor: pointer;
    margin-top: 10px;
    border-radius: 2px;
}