.img-scaler{
 
    color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 15px;
   
  }
  .img-scaler-bar{
    -webkit-appearance: none;
    margin-top: 5px;
    height:7px;
    border-radius: 5px;
    background: #d3d3d3;
    outline: none;
    opacity: 0.7;
    -webkit-transition: .2s;
    transition: opacity .2s;
  
  }
  .img-scaler-bar::-webkit-slider-thumb {
  
    -webkit-appearance: none;
    appearance: none;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background: #000000;
    cursor: pointer;
  }